<template>
<div class="index" ref="main">
	<div class="aside">
		<img src="https://wimg.588ku.com/gif620/21/09/19/b1e4f08a56f3a38493e4b42750cfaf73.gif" class="bg-img" />
		<div
			class="box-3d"
			ref="box"
			@click="toLogin"
      @mouseover="doTrans($event, true)"
      @mouseout="doTrans($event, false)"
		>
			<div class="box-content-3d" :style="tranStyle">
				<img
					v-for="i in 5"
					:key="i"
					src="../../static/img/ava.jpg"
					class="avatar-img"
				/>
			</div>
		</div>
	</div>
	<div class="main">
		<el-tabs v-model="activeName" @tab-click="tabSwitch">
			<el-tab-pane
				v-for="tab in datas"
        :key="tab.id"
        :label="tab.tabName"
        :name="tab.name"
			>
				<div
					v-for="sub in tab.list"
					:key="sub.id"
					class="log lightSpeedIn animated"
				>
					<div class="title">
						{{sub.title}}
					</div>
					<div class="log-con">
						<p>{{sub.desc}}</p>
						<p v-if="sub.a">
							<a :href="sub.a" target="_blank">{{sub.a}}</a>
						</p>
					</div>
				</div>
			</el-tab-pane>
			<el-tab-pane label="我的技能掌握度" name='skill'>
				<div class="skill">
					<div class="skill-bg"></div>
					<div
						v-for="ball in balls"
						:key="ball.id"
						class="skill-item"
						:class="{'rotateIn animated': ballAni}"
					>
						<v-Ball
							:color='ball.color'
							:percent="ball.percent"
						></v-Ball>
						<p class="word">
							{{ball.txt}}
						</p>
					</div>
				</div>
			</el-tab-pane>
		</el-tabs>
	</div>
</div>
</template>

<script>
import VueGallery from "vue-gallery";
import Ball from "@/components/Ball";
import { datas, balls } from "@/datas/data.js";
export default {
  name: "index",
  components: {
    "v-Ball": Ball,
    gallery: VueGallery,
  },
  data() {
    return {
      datas,
			balls,
      list: [],
      imgUrl: "http://imagecloud.ghmagical.com/t_nw10.jpg",
      imgUrl2: "http://imagecloud.ghmagical.com/t_nw11.jpg",
      activeName: "log",
      ballAni: false,
      moveOut: false,
      images: ["../../static/img/ava.jpg"],
      tranStyle: "transform: translateZ(-85px) rotateY(0deg) rotateX(0deg)",
      index: null,
      x1: 0,
      y1: 0,
      x2: 0,
      y2: 0,
      x0: 0,
      y0: 0,
      k: 0,
      musicPause: false,
    };
  },

  methods: {
    tabSwitch(tab, event) {
      if (tab.name == "skill") {
        setTimeout(() => {
          this.ballAni = true;
        }, 1000);
      } else {
        this.ballAni = false;
      }
    },
    toLogin() {
      this.$router.push("/");
    },
    doTrans($event, flag) {
      let e = $event;
      let [x, y] = [e.clientX, -e.clientY];
      let K = (y - this.y0) / (x - this.x0);
      if (this.k < K && K < -this.k) {
        if (x > this.x0) {
          this.tranStyle = flag
            ? "transform: translateZ(-85px) rotateY(-90deg) rotateX(0deg)"
            : "transform: translateZ(-85px) rotateY(0deg) rotateX(0deg)";
        } else {
          this.tranStyle = flag
            ? "transform: translateZ(-85px) rotateY(90deg) rotateX(0deg)"
            : "transform: translateZ(-85px) rotateY(0deg) rotateX(0deg)";
        }
      } else {
        if (y > this.y0) {
          this.tranStyle = flag
            ? "transform: translateZ(-85px) rotateY(0deg) rotateX(-90deg)"
            : "transform: translateZ(-85px) rotateY(0deg) rotateX(0deg)";
        } else {
          this.tranStyle = flag
            ? "transform: translateZ(-85px) rotateY(0deg) rotateX(90deg)"
            : "transform: translateZ(-85px) rotateY(0deg) rotateX(0deg)";
        }
      }
    },
    controlAudio() {
      // const audio = this.$refs.audio;
      // if(audio.paused) {
      // 	audio.play();
      // 	this.musicPause = false;
      // }else{
      // 	audio.pause();
      // 	this.musicPause = true;
      // }
    },
  },

  created() {},

  mounted() {
    const oDiv = this.$refs.box;
    this.x1 = oDiv.offsetLeft;
    this.y1 = -oDiv.offsetTop;
    this.x2 = this.x1 + oDiv.offsetWidth;
    this.y2 = this.y1 - oDiv.offsetHeight;
    this.x0 = (this.x1 + this.x2) / 2;
    this.y0 = (this.y1 + this.y2) / 2;
    this.k = (this.y2 - this.y1) / (this.x2 - this.x1);
  },
};
</script>

<style lang="stylus" scoped>
@keyframes roX {
  0% {
    transform: rotateX(180deg);
  }

  100% {
    transform: rotateX(0deg);
  }
}

@keyframes light-move {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 0 240px;
  }
}

.index {
  height: 100%;
  position: relative;
}

.aside {
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  display: flex;
  width: 22%;
  flex-wrap: wrap;
  position: relative;

  .bg-img {
    display: block;
    max-width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    transition: all 0.618s;

    &.out-side {
      left: -100%;
      z-index: 1;
    }
  }
}

.box-3d {
  height: 170px;
  width: 170px;
  position: absolute;
  top: 170px;
  left: 50%;
  margin-left: -85px;
  box-shadow: 0 0 20px #fff;
  cursor: pointer;
  perspective: 1000px;

  .box-content-3d {
    width: 170px;
    height: 170px;
    position: absolute;
    transform-origin: 50%;
    transform: translateZ(-85px) rotateY(0deg) rotateX(0deg);
    transition: transform 0.5s linear;
    transform-style: preserve-3d;

    .avatar-img {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;

      &:nth-child(1) {
        transform: translateZ(84px);
      }

      &:nth-child(2) {
        transform: rotateY(90deg) translateZ(84px);
      }

      &:nth-child(3) {
        transform: rotateY(-90deg) translateZ(84px);
      }

      &:nth-child(4) {
        transform: rotateX(-90deg) translateZ(84px);
      }

      &:nth-child(5) {
        transform: rotateX(90deg) translateZ(84px);
      }
    }
  }
}

.main {
  position: absolute;
  top: 0;
  left: 22%;
  right: 0;
  bottom: 0;
  background: #fff;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  overflow: auto;

  .log {
    margin: 15px 0 0 0;
    background: #e3e1e6;
    padding: 10px;
    border-radius: 10px;

    &:first-child {
      margin-top: 5px;
    }

    .title {
      font-size: 24px;
      color: #000;
      font-weight: bold;
      padding-bottom: 15px;
      border-bottom: 1px solid #eee;
    }
  }

	.skill {
      width: 98%;
      min-height: 240px;
      margin: 30px auto;
      background: rgba(22, 68, 130, 0.3);
      box-shadow: 0 0 10px 10px rgba(44, 168, 219, 0.4);
      border-radius: 10px;
      display: flex;
      position: relative;
      animation: roX 1s linear;
      flex-wrap: wrap;

      .skill-bg {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border-radius: 10px;
        background: linear-gradient(-30deg, rgba(27, 212, 201, 0.3), rgba(22, 68, 130, 0.3));
      }

      .skill-item {
        flex: 1;
        position: relative;
        min-width: 25%;
        min-height: 240px;

        .word {
          position: absolute;
          left: 0;
          right: 0;
          top: 74%;
          text-align: center;
          font-size: 20px;
          color: #fff;
        }
      }
    }
}
</style>


